import React, {Component} from 'react'
import axios from 'axios'
import { withAuth0 } from '@auth0/auth0-react';

class Movies extends Component 
{
	constructor(props) {
		super(props)

		this.state =  {
			movies: [],
            loading: true,
            error: ''
		}
	}

	componentDidMount() {
		this.populateMoviesData()
	}

	populateMoviesData = async () => {
        const { getAccessTokenSilently } = this.props.auth0;
        const domain = "https://localhost:5000";
        const accessToken = await getAccessTokenSilently({
            audience: domain,
            scope: "read:movies",
        });
        
        axios.get('/movies',
        {headers: {Authorization: `Bearer ${accessToken}`}}).then(response => {
			this.setState({
				movies: response.data.movies,
				loading: false
			})
		})
    }

    redirectToUpdate(movidId) {
        const {history} = this.props;
        history.push(`/update_movie/${movidId}`);
    }

    deleteClicked = async (movieId) => {
        const { getAccessTokenSilently } = this.props.auth0;
        const domain = "https://localhost:5000";
        const accessToken = await getAccessTokenSilently({
            audience: domain,
            scope: "delete:movie",
        });
        
        let me = this;
        axios.delete(`/movies/${movieId}`,
        {headers: {Authorization: `Bearer ${accessToken}`}}).then(response => {
            this.populateMoviesData();
        }).catch(function(error) {
            if (error.response) {
                me.setState({error: error.response.data});
            }
            else {
                alert("Something went wrong! Please try again.")
            }
        });
    }

	renderMoviesTable(movies) {
		return (
			<table className="table table-striped">
				<thead>
					<tr>
						<th>Title</th>
						<th>Release Date</th>
					</tr>
				</thead>
				<tbody>
					{
						movies.map(movie => (
							<tr key={movie.id}>
								<td>{movie.title}</td>
								<td>{movie.release_date}</td>
                                <td>
                                    <div className="form-group">
                                        <button className="btn btn-primary" onClick={() => this.redirectToUpdate(movie.id)}>Update</button>
                                        <button className="btn btn-danger" onClick={() => this.deleteClicked(movie.id)}>Delete</button>
                                    </div>
                                </td>
							</tr>
						))
					}
				</tbody>
			</table>
		)
	}

	render() {
		let content = this.state.loading ? (
			<p>Loading movies...</p>
		) :
		(
			this.renderMoviesTable(this.state.movies)
		)
        
        if (this.state.error) {
            alert("You do not have permission to delete a movie.");
        }

		return (
			<div>
				<h1>All Movies</h1>
				{content}
			</div>
		)
	}
}

export default withAuth0(Movies);